import { ApiBaseService } from './../../services/api-base.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  loginError: string;

  constructor(private apiBaseService: ApiBaseService,
    private router: Router) { }

  ngOnInit() {
  }

  login(formData) {
    this.apiBaseService.post('/api/login', formData).subscribe((response: any) => {
      if(response.Success) {
        localStorage.setItem('currentUser', JSON.stringify(response.Success.Data[0]));
        this.router.navigate(['']);
      } else {
        this.loginError = 'Email or password is incorrect.'// response.Error.message
      }
    });
  }

}



