import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs';
import { WebcamImage } from 'ngx-webcam';
import { ApiBaseService } from './../../services/api-base.service';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

  private userDetails;
  roles;
  selectedRole;
  addUserPopup: boolean = false;
  raiseDefectPopup: boolean = false;
  public showWebcam = false;
  public webcamImage: WebcamImage = null;
  defectCategories = ["Major", "Minor", "Severe"];
  category;
  uploadedimage;
  private defectTableData: any[];
  private defectTableDataCols = [
    /*  { field: 'creatorId', header: 'creatorId' }, */
    { field: 'assigneeId', header: 'Assignee' },
   /* { field: 'createDate', header: 'createDate' }, */
    /* { field: 'targetDate', header: 'targetDate' }, */
    /* { field: 'FIRNCR', header: 'FIRNCR' }, */
    /* { field: 'location', header: 'location' }, */
/*     { field: 'structure', header: 'structure' }, */ 
    /* { field: 'description', header: 'description' }, */
    { field: 'category', header: 'Category' },
    /* { field: 'impact', header: 'impact' }, */
    /* { field: 'recommendation', header: 'recommendation' }, */
    { field: 'status', header: 'Status' },
    { field: 'imageUrl', header: 'Image' }
  ];

  private trigger: Subject<void> = new Subject<void>();

  constructor(private apiBaseService: ApiBaseService,
    private router: Router) {
  }

  ngOnInit() {
    this.userDetails = JSON.parse(localStorage.getItem('currentUser'));
    this.roles = ["PM-Client", "PM-Consultant", "PM-Contractor", "Civil Engineer", "MEP"]
    this.selectedRole = this.roles[0];
    this.category = this.defectCategories[0];
    this.getDefectTableData();
  }

  addUserPopupDialogue() {
    this.addUserPopup = true;
  }

  raiseDefectPopupDialogue() {
    this.raiseDefectPopup = true;
  }

  getDefectTableData() {
    this.apiBaseService.get('/api/createdtasks').subscribe((response: any) => {
      this.defectTableData = response.Seccess.CreatedTasks;
    });
  }

  signup(formData) {
    this.apiBaseService.post('/api/signup', formData).subscribe((response: any) => {
    });
  }

  raiseDefectForm(formData) {
    this.apiBaseService.post('/api/tasks/createnew', formData).subscribe((response: any) => {
      this.getDefectTableData();
    });
  }

  public triggerSnapshot(): void {
    this.trigger.next();
  }

  public toggleWebcam(): void {
    this.showWebcam = !this.showWebcam;
  }

  public handleImage(webcamImage: WebcamImage): void {
    this.webcamImage = webcamImage;
    let binary = atob(this.webcamImage.imageAsDataUrl.split(',')[1]);
    var array = [];
    for (var i = 0; i < binary.length; i++) {
      array.push(binary.charCodeAt(i));
    }
    this.uploadedimage = new Blob([new Uint8Array(array)], { type: 'image/jpeg' });
  }

  public blobToFile = (theBlob: Blob, fileName: string): File => {
    var b: any = theBlob;
    //A Blob() is almost a File() - it's just missing the two properties below which we will add
    b.lastModifiedDate = new Date();
    b.name = fileName;

    //Cast to a File() type
    return <File>theBlob;
  }

  public get triggerObservable(): Observable<void> {
    return this.trigger.asObservable();
  }

}



