import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';


@Injectable()
export class ApiBaseService {

  private apiToken: string
  constructor(private httpClient: HttpClient) {
    
  }

  private createURL(url: string) {
    return `http://139.59.1.140${url}`;
  }

  private createFormData(formData) {
    let payload = new FormData();
    for (var key in formData) {
      payload.append(key, formData[key]);
    }
    return payload;
  }

  post(url: string, payload) {
    this.apiToken = localStorage.getItem('currentUser') ? JSON.parse(localStorage.getItem('currentUser')).apiToken : undefined;
    const headers = this.apiToken ? new HttpHeaders().append('Authorization', this.apiToken) : new HttpHeaders();
    return this.httpClient.post(this.createURL(url), this.createFormData(payload), { headers });
  }

  get(url: string) {
    this.apiToken = localStorage.getItem('currentUser') ? JSON.parse(localStorage.getItem('currentUser')).apiToken : undefined;
    const headers = this.apiToken ? new HttpHeaders().append('Authorization', this.apiToken) : new HttpHeaders();
    return this.httpClient.get(this.createURL(url), { headers });
  }

}



